.site-header-wrap {
  margin-bottom: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px 0 rgba(72, 48, 143, 0.2);
}
.site-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 30px;
}
.logo-wrap {
  display: flex;
  align-items: center;
}
.logo-text {
  margin-left: 20px;
  color: #000;
  font-family: Philosopher, sans-serif;
  font-size: 28px;
  font-weight: 700;
}
.logo-text small {
  display: block;
  font-family: Roboto, sans-serif;
  font-size: 15px;
  font-weight: 300;
  text-align: left;
}
.site-nav {
  position: relative;
}
.site-nav > ul {
  display: flex;
}
.site-nav > ul > li + li {
  margin-left: 40px;
}
.site-nav .site-nav__dropdown {
  display: none;
  position: absolute;
  top: calc(100% + 20px);
  left: 0;
  z-index: 101;
  padding: 15px 15px 15px 30px;
  border: 1px solid #e36f1d;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 20px 0 rgba(72, 48, 143, 0.2);
}
.site-nav .site-nav__dropdown::before,
.site-nav .site-nav__dropdown::after {
  content: '';
  bottom: 100%;
  left: 20px;
  border: solid transparent;
  position: absolute;
  pointer-events: none;
}
.site-nav .site-nav__dropdown::before {
  border-color: rgba(194, 225, 245, 0);
  border-bottom-color: #e36f1d;
  border-width: 8px;
  margin-left: -8px;
}
.site-nav .site-nav__dropdown::after {
  border-color: rgba(136, 183, 213, 0);
  border-bottom-color: #fff;
  border-width: 7px;
  margin-left: -7px;
}
.site-nav .site-nav__dropdown li {
  color: #444;
}
.site-nav .site-nav__dropdown li.current {
  list-style-type: disc;
}
.site-nav .site-nav__dropdown li.current a {
  font-weight: 500;
}
.site-nav .site-nav__dropdown a {
  line-height: 1.45;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.3px;
}
.site-nav--header > ul > li > a {
  padding: 20px 0;
}
.site-nav__dropdown-link:hover .site-nav__dropdown {
  display: initial;
}
/**
 * форма авторизации
 * старт
 */
.user-wrap {
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
}
.user-wrap:hover .auth-form-wrap[data-header-form] {
  display: flex;
}
.user-wrap:hover .user__logout {
  display: block;
}
.auth-form-wrap {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1;
  flex-direction: column;
  align-items: center;
  width: 225px;
  padding: 15px 20px;
  border: 1px solid #6dc5dd;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(72, 48, 143, 0.2);
}
.auth-form-wrap::before,
.auth-form-wrap::after {
  content: '';
  bottom: 100%;
  border: solid transparent;
  position: absolute;
  pointer-events: none;
}
.auth-form-wrap::before {
  right: 15px;
  border-color: rgba(194, 225, 245, 0);
  border-bottom-color: #6dc5dd;
  border-width: 8px;
}
.auth-form-wrap::after {
  right: 16px;
  border-color: rgba(136, 183, 213, 0);
  border-bottom-color: #fff;
  border-width: 7px;
}
.auth-form-wrap .input {
  margin-bottom: 10px;
}
.auth-form-wrap--sign-in .auth__form {
  padding-bottom: 30px;
  border-bottom: 1px solid #e1e0e0;
}
.auth-form__title {
  margin-bottom: 10px;
  color: #2b3033;
  font-size: 17px;
  font-weight: 700;
}
.auth-form__change-form-btn {
  margin-bottom: 20px;
  color: #3d4348;
  font-size: 14px;
  font-weight: 300;
  text-decoration: underline;
  border: none;
  background-color: transparent;
}
.auth-form__or {
  position: relative;
  top: -12px;
  padding-left: 15px;
  padding-right: 15px;
  font-family: Roboto, sans-serif;
  font-weight: 600;
  background-color: #fff;
}
.user__username {
  display: flex;
  align-items: center;
  color: #4c5e70;
  font-size: 14px;
}
.user__username img {
  margin-left: 20px;
  border-radius: 50%;
}
.user__logout {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1;
  padding: 20px;
  border: 1px solid #6dc5dd;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(72, 48, 143, 0.2);
}
.user__logout::before,
.user__logout::after {
  content: '';
  bottom: 100%;
  border: solid transparent;
  position: absolute;
  pointer-events: none;
}
.user__logout::before {
  right: 15px;
  border-color: rgba(194, 225, 245, 0);
  border-bottom-color: #6dc5dd;
  border-width: 8px;
}
.user__logout::after {
  right: 16px;
  border-color: rgba(136, 183, 213, 0);
  border-bottom-color: #fff;
  border-width: 7px;
}
.user__logout a {
  display: block;
  padding: 5px 17px;
  font-size: 15px;
  border: 1px solid #6dc5dd;
  border-radius: 3px;
  background-color: #fff;
}
.user__logout a:hover {
  color: #6dc5dd;
}
.btn--header-submit {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
}
