  .site-header-xs__top .logo-text small {
    display: none;
  }
}
.header-xs__btn {
  display: block;
  padding: 5px 10px;
  margin-left: auto;
  color: #fff;
  border: none;
  border-radius: 3px;
  background-color: #6dc5dd;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.12);
