}
.header-xs-nav-toggle {
  position: relative;
  width: 24px;
  height: 20px;
  margin-left: 15px;
  border: none;
  background: none;
}
.header-xs-nav-toggle::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 3px;
  margin: auto;
  border-radius: 2px;
  background-color: #000;
  box-shadow: 0 8px 0 0 #000, 0 -8px 0 0 #000;
}
.header-xs-nav-toggle.active::before,
.header-xs-nav-toggle.active::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  margin: auto;
  border-radius: 2px;
  background-color: #000;
  box-shadow: none;
}
.header-xs-nav-toggle.active::before {
  transform: rotate(45deg);
}
.header-xs-nav-toggle.active::after {
  transform: rotate(-45deg);
}
.site-header-xs__bottom {
